:root[data-theme="theme-light"] {
  --primary-bg-color: rgba(255, 255, 255, 1);
  --secondary-bg-color: #ddd;
  --primary-text-color: #000;
  --secondary-text-color: #666;
}
:root[data-theme="theme-dark"] {
  --primary-bg-color: rgba(21, 21, 21, 1);
  --secondary-bg-color: rgba(32, 32, 34, 1);
  --primary-text-color: rgba(255, 255, 255, 1);
  --secondary-text-color: rgba(148, 148, 149, 1);
}

/* scrollbar */
::-webkit-scrollbar { background: var(--primary-bg-color); width: 10px; }
::-webkit-scrollbar-thumb { background: var(--secondary-bg-color); border-radius: 10px; }
html { scrollbar-color: var(--secondary-bg-color); scrollbar-width: thin; }

body {
  font-family: 'Open Sans',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  background: var(--primary-bg-color);
  color: var(--primary-text-color);
}
.section-heading {
  font-size: 15px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 3.5px;
  padding: 50px 0;
  position: relative;
  span {
      background: var(--primary-bg-color);
      padding: 0 20px; 
  }
  &:after {
      content:'';
      position: absolute;
      width: 100%;
      height: 4px;
      background: var(--secondary-bg-color);
      top: 50%;transform: translateY(-50%);
      left: 0;
      z-index: -1;
  }
}

.section-left {
  padding-top: 70px;
  padding-left: 70px;
  height: 100vh;
  position: fixed;
  max-width: 480px;
  h1 {
    font-size: 60px;
    font-weight: 800;
    margin: 0 0 15px 0;
  }
  h5 {
    font-size: 24px;
    font-weight: 500;
    margin: 0;
    color: var(--secondary-text-color);
    margin-bottom: 40px;
  }
  .links-left-wrp {
    display: flex;
    align-items: center;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 12px;
    letter-spacing: 3px;
    margin-bottom: 15px;
    svg {
      width: 22px;
      height: 22px;
      margin-right: 20px;
      .st10{fill:#fff;stroke:#fff;stroke-width:14;stroke-miterlimit:10;}
      .cls-1{fill:#fff;stroke:#fff;stroke-linecap:round;stroke-linejoin:round;stroke-width:2px;}
    }
  }
}
.section-right {
  max-width: calc(100% - 600px);
  position: absolute;
  padding: 20px 70px 70px 0;
  right: 0;
  top:0;
}
.web-links {
  margin-bottom: 50px;
}
.profile-wrp {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  overflow: hidden;
  img {
    width: 100%;
    height: auto;
  }
}

@media screen and (max-width: 1180px) {
  .section-left {
    padding-left: 30px;
    max-width: 350px;
    h1 {
      font-size: 50px;
    }
  }
  .section-right {
    max-width: calc(100% - 380px);
    padding: 10px 30px 30px 0;
  }
}

@media screen and (max-width: 1024px) {
  .section-left,
  .section-right {
    max-width: 100%;
    position: relative;
    height: auto;
    padding: 0 30px
  }
  .section-left {
    h1,h5 {
      text-align: center;
    }
    h1 {
      margin-top: 30px;
    }
    .web-links {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0;
      .links-left-wrp {
        margin: 0 20px;
      }
    }
    .profile-wrp {
      position: fixed;
      bottom: 30px;
      left: 30px;
      z-index: 2
    }
  }
}

@media screen and (max-width: 767px) {
  .section-left,
  .section-right {
    padding: 0 15px
  }
  .section-left {
    h1 {
      font-size: 42px;
      font-weight: 700;
    }
    h5 {
      font-size: 19px;
    }
    .web-links {
      flex-wrap: wrap;
      .links-left-wrp {
        margin: 0 10px 10px 10px;
      }
    }
    .profile-wrp {
      bottom: 15px;
      left: 15px;
    }
  }
  .section-heading {
    padding: 30px 0;
  }
  
}